import React from 'react';
import { Link } from 'gatsby'
import Layout from '../layout';

const AtelierItem = ({ data }) => {
    const atelier = data.strapiAtelier
    return (
        <Layout>
            <div className="atelier__item">
                <p>{atelier.titre}</p>
                <p>{atelier.description}</p>
            </div>
            <Link to="/subscription" state={{atelier: atelier}}>S'inscrire</Link>
        </Layout>
    );
};

export default AtelierItem;

export const query = graphql`
  query AtelierItem($slug: String!) {
    strapiAtelier(id: {eq: $slug}) {
        Date(formatString: "DD MM YY")
        type
        titre
        description
        infos
        id
    }
  }
`